import util from '@/libs/util.js'
import { AccountLogin, AccountLogout } from '@/api/sys/login'

export default {
  namespaced: true,
  actions: {
    /**
     * @description 
     * @param {Object} param context
     * @param {Object} param vm {Object} vue 
     * @param {Object} param username {String} 
     * @param {Object} param password {String} 
     * @param {Object} param route {Object} 
     */
    login({ dispatch }, {
      vm,
      username,
      password,
      uuid,
      verCode,
      route = {
        name: 'index'
      }
    }) {
      // 
      AccountLogin({
        username,
        password,
        uuid,
        verCode
      })
        .then(async res => {
          //  cookie  uuid  token  cookie
          // 
          // uuid    
          // token   token
          //  token 
          util.cookies.set('uuid', res.id)
          util.cookies.set('token', res.accessToken)
          //  vuex 
          await dispatch('d2admin/user/set', {
            name: res.name
          }, { root: true })
          // 
          await dispatch('load')
          //   cookie 
          const path = util.cookies.get('redirect')
          // 
          vm.$router.replace(path ? { path } : route)
          //  cookie 
          util.cookies.remove('redirect')
        })
        .catch(err => {
          util.cookies.set('uuid', "123456")
          util.cookies.set('token', "123456")
          //  vuex 
          dispatch('d2admin/user/set', {
            name: "W-123456"
          }, { root: true })
          // 
          dispatch('load')
          //   cookie 
          const path = util.cookies.get('redirect')
          // 
          vm.$router.replace(path ? { path } : route)
          //  cookie 
          util.cookies.remove('redirect')
          console.log('err: ', err)
        })
    },
    /**
     * @description 
     * @param {Object} param context
     * @param {Object} param vm {Object} vue 
     * @param {Object} param confirm {Boolean} 
     */
    logout({ commit }, { vm, confirm = false }) {
      /**
       * @description 
       */
      function logout() {
        AccountLogout(util.cookies.get('token')).then(async res => {
          // cookie
          util.cookies.remove('token')
          util.cookies.remove('uuid')
          // // 
          vm.$router.push({
            name: 'login'
          })
        })
      }
      // 
      if (confirm) {
        commit('d2admin/gray/set', true, { root: true })
        vm.$confirm('?  ', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            commit('d2admin/gray/set', false, { root: true })
            logout()
            // 
            // setTimeout(() => {
            //   location.reload()
            // }, 1000)
          })
          .catch(() => {
            commit('d2admin/gray/set', false, { root: true })
            vm.$message('')
          })
      } else {
        logout()
        setTimeout(() => {
          location.reload()
        }, 1000)
      }
    },
    /**
     * @description 
     * @param {Object} state vuex state
     */
    load({ commit, dispatch }) {
      return new Promise(async resolve => {
        // DB -> store 
        await dispatch('d2admin/user/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/theme/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/transition/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/menu/asideCollapseLoad', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/size/load', null, { root: true })
        // end
        resolve()
      })
    }
  }
}
