import store from '@/store'
import axios from 'axios'
import router from '@/router/index'
import { Message } from 'element-ui'
import util from '@/libs/util'
import loading from '@/libs/loading'
import permission from '@/libs/permission'

// 
function errorLog(err) {
  // 
  store.dispatch('d2admin/log/add', {
    type: 'error',
    err,
    info: ''
  })
  // 
  if (process.env.NODE_ENV === 'development') {
    util.log.danger('>>>>>> Error >>>>>>')
    console.log(err)
  }
  // 
  Message({
    message: err.message,
    type: 'error',
    duration: 5 * 1000
  })
}

//  axios 
const service = axios.create({
  baseURL: process.env.VUE_APP_API,
  timeout: 30000 // 
})

// 
service.interceptors.request.use(
  config => {
    if (!permission.access(config, store)) {
      // eslint-disable-next-line no-throw-literal
      throw {
        type: '403',
        config: config
      }
    }
    loading.show(config)
    // 
    if (!(/^https:\/\/|http:\/\//.test(config.url))) {
      const token = util.cookies.get('token')
      // config.headers['Content-Type'] = 'application/json'
      if (token && token !== 'undefined') {
        // token-- ['Authorization']key 
        config.headers['Authorization'] = 'Bearer ' + token
      }
    }
    return config
  },
  error => {
    // 
    console.log(error)
    Promise.reject(error)
  }
)

// 
service.interceptors.response.use(
  response => {
    loading.hide(response.config)
    const res = response.data
    if (res.statusCode && res.statusCode !== 200) {
      Message({
        message: res.message,
        type: 'error',
        duration: 3 * 1000
      })
      return Promise.reject(res.message)
    } else {
      // message(response.config)
      console.log('===>res', res)
      return res.hasOwnProperty('data') ? res.data : res
    }
  },
  error => {
    console.log(error)
    loading.hide(error.config)
    if (error.response && error.response.status === 401) {
      util.cookies.remove()
      if (error.config.url.indexOf('logout') === -1) {
        Message({
          message: ',!',
          type: 'error',
          duration: 3 * 1000
        })
      }
      setTimeout(() => {
        // cookie
        util.cookies.remove('token')
        util.cookies.remove('uuid')
        // 
        router.push({
          name: 'login'
        })
      }, 1000)
    } else if (error.response && error.response.status === 500) {
      errorLog(new Error(`!: ${error.config.url}, code = ${error.response.status}`))
    } else if (error.message && error.message.indexOf('timeout') > -1) {
      errorLog(new Error(`!: ${error.config.url}`))
    } else if (error.type === '403') {
      errorLog(new Error(`!: ${error.config.url}`))
    } else {
      errorLog(new Error(`!: ${error.config.url}`))
    }
    return Promise.reject(error)
  }
)

export default service
